body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  width: 100%;
  display: inline-block;
  background-color: #eee;
  box-shadow: 1px 3px 4px #ccc;
  height: 55px;
  position: fixed;
  z-index: 1000;
}
.inline {
  display: inline-block;
  margin-left: 10px;
  margin-top: 0px;
}
.item {
  margin: 10px;
  box-shadow: 1px 3px 4px rgb(117, 117, 117);
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 16px;
  background-color: white;
  width: 200px;
}
.cart {
  box-shadow: 1px 3px 4px #eee;
  border: 1px solid #ccc;
  border-radius: 3px;
  padding: 16px;
  margin: 10px 30px 0px 0px;
  background-color: rgb(196, 223, 255);
}
body {
  background-color: rgb(235, 234, 234);
}
.search {
  position: "relative";
  border-radius: 5px;
  margin-left: 3px;
  width: "auto";
  margin-right: 2px;
  margin-top: 0px;
  background-color: rgba(211, 210, 210, 0.562);
  color: white;
  padding: 1px 5px;
}
.search-icon {
  width: 9;
  height: "100%";
  position: "absolute";
  pointer-events: "none";
  display: "flex";
  -webkit-align-items: "center";
          align-items: "center";
  -webkit-justify-content: "center";
          justify-content: "center";
}
.input-root {
  color: "inherit";
  width: "100%";
}
.input-input {
  padding-top: 1px;
  padding-right: 1px;
  padding-bottom: 1px;
  padding-left: 10px;
  width: "100%";
  color: white;
  background-color: #929ac5;
  border: 0px;
  font-size: 1.3em;
}
.input-input::-webkit-input-placeholder {
  color: white;
  background-color: #929ac5;
  border: 0px;
}
.input-input::-ms-input-placeholder {
  color: white;
  background-color: #929ac5;
  border: 0px;
}
.input-input::placeholder {
  color: white;
  background-color: #929ac5;
  border: 0px;
}
.input-input:focus {
  border: 0px;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

